@charset "utf-8";

// fonts
$rem-base: 10px;

$font-size: 1.6rem;
$font-size-h1: 3.0rem;
$font-size-h2: 2.4rem;
$font-size-h3: 2.0rem;
$font-size-h4: 1.8rem;
$font-size-h5: 1.8rem;
$font-size-h6: 1.8rem;

$line-height: 1.5;

$font-family:
  // Safari for OS X and iOS (San Francisco)
  -apple-system,
  // Chrome for OS X (San Francisco)
  BlinkMacSystemFont,
  // Windows
  "Segoe UI",
  // Android
  "Roboto",
  // Linux
  "Oxygen", // KDE
  "Ubuntu",
  "Cantarell", // GNOME
  // Firefox OS [R.I.P.]
  "Fira Sans",
  // Older Android
  "Droid Sans",
  // Chinese font for OSX and iOS
  "Hiragino Sans GB",
  // Chinese font for Windows
  "Microsoft Yahei", "微软雅黑",
  // Basic web fallback
  Arial, Helvetica, STHeiti, sans-serif,
  // Emoji
  "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";

$font-family-code: Consolas, "Liberation Mono", Menlo, Courier, monospace;

// layouts
$container-padding-width: 16px;
$scrollbar-width: 17px;

$container-padding: 0 $container-padding-width 2 * $container-padding-width;
$container-side-padding:  0 2 * $container-padding-width 2 * $container-padding-width;

$container-min-width: 320px;
$container-max-width: 768px;
$container-side-width: 256px;
$container-breakpoint-width: 1024px + $scrollbar-width;

$section-margin: 2 * $container-padding-width 0 0 0;
$paragraph-margin: $container-padding-width 0 0 0;

// colors
$color-text-black: #333;
$color-text-blockquote: #777;
$color-text-link: #07d;

$color-border: #ddd;
$color-body-background: #f6f6f6;
$color-code-background: #f6f6f6;
$color-th-background: #f6f6f6;
